.features {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  flex: 1;

  /* background: linear-gradient(180deg, #f5c470 100%, rgba(255, 255, 255, 0)); */
  background: linear-gradient(180deg, #fec15f 100%, rgb(255 255 255 / 50%));

  /* height: 1800px;  */
  height: auto;


  /* align-items: flex-start;
    justify-content: flex-start; */

  /* padding: var(--padding-2xl-5) 0;
    gap: var(--gap-19xl); */
  max-width: 100%;
  text-align: center;
  font-size: 26px; 

  /* font-size: minmax(18px, 20px);  */

  /* font-size: var(--font-size-21xl); */
  color: var(--color-black);
  font-family: var(--font-inter);

  /* /* new */
  overflow: clip visible;
}

.wrapper-progress-icon {
  align-self: stretch;
  height: 60px;
  position: relative;

  /* max-width: 100%; */

  /* flex-shrink: 0; */
  width: 100%;
  min-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  /* border: red solid 1px;  */

  /* new
    overflow: hidden;  */
}

.progress-icon {
  align-self: stretch;

  /* height: 100%; */
  height: auto;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: fill;
  position: absolute;
  left: -3%;
  top: 0;
  width: 110%;
  min-width: 110%;
  transform: translateY(-50%);

  /* transform: scale(10.067); */
}

.btn-1 {
  font-family: Hack, monospace;
  background: #0F0F6D;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  padding: 0.8rem;
  border: 0;
  transition: all 0.5s;
  border-radius: 10px;
  width: auto;
  position: relative;
  margin-bottom: 30px;

  &::after {
    content: '\2193';
    font-family: "Font Awesome 5 Pro";
    font-weight: 400;
    position: absolute;
    inset: 31% 5% 0 85%;
    opacity: 0;

  }

  &:hover {
    background: #2b2bff;
    transition: all 0.5s;
    border-radius: 10px;
    box-shadow: 0 6px 15px #0000ff61;
    padding: 1rem 2rem 1rem 1rem;

    &::after {
      opacity: 1;
      transition: all 0.5s;

    }
  }

}

.btn-1:active {
  background: black;
}

.btn-1:focus .feature-text-box {
  display: inherit; 
}


.feature-text-box {
  display: none;
    opacity: 0;
    transition: opacity 0.5s ease;
}

.feature-text-box.show {
  display: block;
  opacity: 1;
}

@media screen and (width >= 2000px) {
  .progress-icon {
    display: none;
  }
}

.features-flexbox {
  display: grid;
  box-sizing: border-box;

  /* align-self: stretch; */
  height: auto;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, minmax(300px, auto));

  /* grid-template-columns: repeat(3, 3fr);
  grid-template-rows: repeat(3, minmax(350px, auto)); */
  row-gap: 20px;

  /* height: 1114px; */

  /* flex-direction: row;
    flex-wrap: wrap;
    align-items:flex-start;
    justify-content: center;
    padding: var(--padding-25xl) 0 var(--padding-31xl); */
  position: relative;

  /* justify-content: space-around; */

  /* flex-basis: 25%; */
  gap: var(--gap-27xl);
  font-size: var(--font-size-medium);
  min-height: 800px;

}

.single-feature {
  width: 20vw;

  /* width: 100%;  */
  max-width: 350px;
  min-width: 250px; 
  height: auto;

  /* height: 650px; Change this */

  /* margin: 0 !important; */

  /* position: absolute; */

  /* top: 44px;
    left: 153.5px; */
  display: flex;
  flex-direction: column;
  flex-basis: calc(100% / 3);
  flex-grow: 0;
  align-items: center;
  justify-content: flex-start;
  padding: 25px 10px;
  box-sizing: border-box;
  gap: var(--gap-17xl);
  box-shadow: 10px 10px 4px rgb(0 0 0 / 25%);
  border-radius: var(--br-25xl);
  background-color: var(--color-papayawhip);

}

